.CenterBox {
    display: flex;
    padding: 30px 43px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    width: 100%;
    height: 100%;
  
    .CenterBoxTitle {
      color: #1C1B1B;
      text-align: center;
      font-family: Interceptor Italic;
      font-size: 34px;
      font-weight: 400;
      line-height: 50.6px;
      letter-spacing: 1.34px;
    }
  
    .CenterBoxContent {
      color: #1C1B1B;
      text-align: center;
      font-family: "Open Sans";
      font-size: 18px;
      line-height: 22.26px;
      letter-spacing: 0.5px;
    }
  }
  
  
  
  .container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    width: 100%;
    padding: 50px;
  }
  
  .div1 {
    display: flex;
    flex-direction: column;
    gap: 10px;
    .shorterImg{
      height: 29rem !important;
      img{
        height: 29rem !important;
      }
    }
  }
  
  .div2 {
    display: flex;
    flex-direction: column;
    gap: 10px;
    .shorterImg{
      height: 29rem !important;
      img{
        height: 29rem !important;
      }
    }
  }
  
  .div3 {
    display: flex;
    flex-direction: column;
    gap: 10px;
    .shorterImg{
      height: 29rem !important;
      img{
        height: 29rem !important;
      }
    }
  }
  
  .longerImg {
    width: 100%;
    height: 100%;
  
  }
  
  .shorterImg,
  .squareImage {
    width: 100%;
    height: 100%;
  
  }
  
  .verticalImage {
    height: 100%;
    width: 100%;
  
  }
  
  @media (max-width: 430px) {
    .container {
      display: none;
    }
  }